@import url("https://fonts.googleapis.com/css2?family=Barlow:wght@600&family=Fraunces:opsz,wght@9..144,700;9..144,900&display=swap");
html {
  box-sizing: border-box;
  font-size: 62.5%; /* 1 rem = 10px */
  scroll-behavior: smooth;
}

*, *:before, *:after {
  box-sizing: inherit;
}

* {
  margin: 0;
}

body {
  font-size: 1.8rem;
}
body.fijar-body {
  overflow: hidden;
}
body.fixed-body {
  padding-top: 8.6rem;
}

h1 {
  margin: 0;
}

img {
  width: 100%;
}

a {
  text-decoration: none;
  font-family: "Barlow", sans-serif;
  font-weight: 600;
}

ul {
  padding: 0;
}

li {
  list-style: none;
  padding: 0;
}

.container {
  max-width: 120rem;
  width: 90%;
  margin: 0 auto;
}

