.header {
  background-color: #3EBFFF;
  width: 100%;
  transition: position 0.4s, transform 0.4s;
}
.header.fixed {
  left: 0;
  top: 0;
  position: fixed;
  width: 100%;
  box-shadow: 0.3rem 0.3rem 0.8rem #333;
  z-index: 10;
}
.header .nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 5rem;
  padding: 3.5rem 0;
}
.header .nav__logo {
  display: inline-flex;
}
.header .nav__logo img {
  height: 3rem;
}
@media (max-width: 992px) {
  .header .nav__menu {
    background-color: #fff;
    border-radius: 1rem 0 1rem 1rem;
    max-width: 76.8rem;
    padding: 3rem;
    position: fixed;
    right: -100%;
    top: -5%;
    transform: translate(-12%, 50%);
    transition: right 0.4s;
    width: 80%;
    box-shadow: 8px 9px 14px 0px #333;
    z-index: 10;
  }
  .header .nav__menu::before {
    content: "";
    position: absolute;
    width: 0;
    top: -12px;
    right: -12px;
    height: 0;
    transform: rotate(45deg);
    border-right: 25px solid transparent;
    border-bottom: 25px solid hsl(0, 0%, 100%);
  }
}
.header .nav .show-menu {
  right: 0%;
}
.header .nav__list {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
}
@media (min-width: 992px) {
  .header .nav__list {
    flex-direction: row;
    gap: 2rem;
  }
}
.header .nav__item {
  padding: 1rem 2rem;
  border-radius: 2rem;
  transition: background-color 0.5s ease, text-transform 0.5s;
}
.header .nav__item:hover {
  background-color: hsl(51, 100%, 49%);
  text-transform: uppercase;
}
@media (min-width: 992px) {
  .header .nav__item:hover {
    background-color: #6bceff;
    text-transform: uppercase;
  }
}
.header .nav__item a {
  color: hsl(212, 27%, 19%);
}
.header .nav__item a:hover {
  font-family: "Fraunces", serif;
}
@media (min-width: 992px) {
  .header .nav__item a {
    color: hsl(0, 0%, 100%);
  }
}
.header .nav__toggle, .header .nav__close {
  cursor: pointer;
}
.header .nav__toggle {
  display: block;
}
@media (min-width: 992px) {
  .header .nav__toggle {
    display: none;
  }
}

.home {
  background-image: url("../../../build/img/mobile/image-header.jpg");
  background-position: center center;
  background-size: cover;
  width: 100%;
  height: 55rem;
}
@media (min-width: 762px) {
  .home {
    background-image: url("../../../build/img/desktop/image-header.jpg");
    background-position: center center;
    background-size: cover;
    height: 70rem;
  }
}
.home__content {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.home__content h1 {
  font-family: "Fraunces", serif;
  font-size: 5rem;
  font-weight: 700;
  color: hsl(0, 0%, 100%);
  text-align: center;
  margin: 15rem 0 3rem 0;
  text-transform: uppercase;
  letter-spacing: 0.5rem;
}
@media (min-width: 762px) {
  .home__content h1 {
    font-size: 6rem;
    margin: 17rem 0 7.5rem 0;
  }
}

.home__img img {
  animation: arrow 2s ease-in-out 0s infinite alternate none;
}
@keyframes arrow {
  0% {
    transform: translateY(5rem);
  }
  100% {
    transform: translateY(0);
  }
}

.layout {
  display: grid;
  grid-template-columns: 1fr;
}
@media (min-width: 762px) {
  .layout {
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(3, 1fr);
  }
  .layout .item1 {
    grid-column: 2/3;
    grid-row: 1/2;
  }
}
.layout .item2,
.layout .item4 {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 5rem 0;
}
@media (min-width: 762px) {
  .layout .item2,
  .layout .item4 {
    padding: 0;
  }
}
.layout__grid .img-mobile {
  display: block;
}
@media (min-width: 762px) {
  .layout__grid .img-mobile {
    display: none;
  }
}
.layout__grid .img-desktop {
  display: none;
}
@media (min-width: 762px) {
  .layout__grid .img-desktop {
    display: block;
  }
}
.layout__grid .description {
  width: 85%;
  max-width: 45rem;
  margin: 0 auto;
  text-align: center;
}
@media (min-width: 762px) {
  .layout__grid .description {
    text-align: left;
  }
}
@media (min-width: 992px) {
  .layout__grid .description {
    padding-inline: 3.5rem;
  }
}
.layout__grid .description h2 {
  font-family: "Fraunces", serif;
  font-weight: 900;
  font-size: 3.5rem;
  color: hsl(212, 27%, 19%);
}
@media (min-width: 992px) {
  .layout__grid .description h2 {
    font-size: 4rem;
  }
}
.layout__grid .description p {
  margin: 2.5rem 0;
  line-height: 1.5;
  font-family: "Barlow", sans-serif;
  color: hsl(232, 10%, 55%);
}
@media (min-width: 762px) {
  .layout__grid .description p {
    font-size: 1.5rem;
  }
}
.layout__grid .description a {
  font-family: "Fraunces", serif;
  text-transform: uppercase;
  color: hsl(212, 27%, 19%);
  font-weight: 900;
}
@media (min-width: 762px) {
  .layout__grid .description a {
    font-size: 1.5rem;
  }
}
.layout--card {
  position: relative;
  width: 100%;
  height: 75rem;
  display: flex;
  justify-content: center;
  align-items: flex-end;
}
@media (min-width: 762px) {
  .layout--card {
    height: auto;
  }
}
.layout--card .img {
  position: absolute;
  background-size: cover;
  background-position: center center;
  height: 100%;
  width: 100%;
}
.layout--card .cereza {
  background-image: url("../../../build/img/mobile/image-graphic-design.jpg");
}
@media (min-width: 762px) {
  .layout--card .cereza {
    background-image: url("../../../build/img/desktop/image-graphic-design.jpg");
  }
}
.layout--card .naranja {
  background-image: url("../../../build/img/mobile/image-photography.jpg");
}
@media (min-width: 762px) {
  .layout--card .naranja {
    background-image: url("../../../build/img/desktop/image-photography.jpg");
  }
}
.layout--card .card--description {
  position: relative;
  z-index: 1;
  width: 85%;
  text-align: center;
}
.layout--card .card--description h2 {
  font-family: "Fraunces", serif;
  font-weight: 900;
  font-size: 3.5rem;
  margin-bottom: 3.5rem;
}
@media (min-width: 762px) {
  .layout--card .card--description h2 {
    margin-bottom: 2.8rem;
  }
}
.layout--card .card--description p {
  font-family: "Barlow", sans-serif;
  line-height: 1.5;
  max-width: 375px;
  margin: 0 auto 5rem auto;
}
@media (min-width: 762px) {
  .layout--card .card--description p {
    font-size: 1.5rem;
    width: 85%;
    margin: 0 auto 4rem auto;
  }
}
@media (min-width: 992px) {
  .layout--card .card--description p {
    font-size: 1.6rem;
    margin: 0 auto 5rem auto;
  }
}

.item2 a,
.item4 a {
  position: relative;
  display: inline-block;
  padding-bottom: 3px;
}
.item2 a::before,
.item4 a::before {
  content: "";
  border-radius: 2rem;
  bottom: 0;
  height: 8px;
  left: -4px;
  position: absolute;
  width: 110%;
  z-index: -100;
  transition: opacity 0.3s ease-in;
}
.item2 a:hover::before,
.item4 a:hover::before {
  opacity: 0.5;
}

.item2 a::before {
  background: hsl(51, 100%, 49%);
}

.item4 a::before {
  background: hsl(7, 99%, 70%);
}

.item5 .card--description h2 {
  color: hsl(167, 40%, 24%);
}
.item5 .card--description p {
  color: hsl(167, 40%, 24%);
}

.item6 h2 {
  color: hsl(198, 62%, 26%);
}
.item6 p {
  color: hsl(198, 62%, 26%);
}

.testimonial {
  width: 85%;
  max-width: 37.5rem;
  margin: auto;
  padding: 6rem 0;
}
@media (min-width: 762px) {
  .testimonial {
    max-width: 700px;
    padding: 8rem 0;
  }
}
@media (min-width: 992px) {
  .testimonial {
    max-width: 1200px;
    padding: 10rem 0;
  }
}
.testimonial__container {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 6rem;
}
@media (min-width: 762px) {
  .testimonial__container {
    flex-direction: row;
  }
}
.testimonial__title {
  font-family: "Fraunces", serif;
  text-transform: uppercase;
  font-size: 1.8rem;
  color: hsl(210, 4%, 67%);
  text-align: center;
  letter-spacing: 0.5rem;
  margin-bottom: 6rem;
}
.testimonial__card {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.testimonial__card .avatar {
  width: 8rem;
  border-radius: 50%;
}
.testimonial__card .card-content {
  text-align: center;
}
.testimonial__card .card-content p {
  font-family: "Barlow", sans-serif;
  color: hsl(213, 9%, 39%);
  line-height: 1.5;
  margin: 3rem 0;
}
.testimonial__card .card-name {
  text-align: center;
}
.testimonial__card .card-name h3 {
  font-family: "Fraunces", serif;
  font-size: 2.2rem;
  font-weight: 900;
  margin-bottom: 1rem;
}
.testimonial__card .card-name span {
  font-family: "Barlow", sans-serif;
  color: hsl(210, 4%, 67%);
}

.galeria {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}
@media (min-width: 762px) {
  .galeria {
    grid-template-columns: repeat(4, 1fr);
  }
}
.galeria .img1 {
  grid-row: 2/3;
}
@media (min-width: 762px) {
  .galeria .img1 {
    grid-row: 1/3;
    grid-column: 3/3;
  }
}
.galeria .mobile {
  display: block;
}
@media (min-width: 762px) {
  .galeria .mobile {
    display: none;
  }
}
.galeria .desktop {
  display: none;
}
@media (min-width: 762px) {
  .galeria .desktop {
    display: block;
  }
}

::-webkit-scrollbar {
  width: 0.6rem;
  border-radius: 0.5rem;
  backdrop-filter: blur(24px);
  background-color: #3EBFFF;
}

::-webkit-scrollbar-thumb {
  background-color: #a2e0ff;
}
::-webkit-scrollbar-thumb:hover {
  background-color: hsl(51, 100%, 49%);
}

.footer {
  background-color: hsl(167, 44%, 70%);
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 8rem 0;
}
.footer__container .footer-logo {
  width: 18rem;
  margin: 0 auto;
}
.footer__container .footer-nav {
  display: flex;
  flex-direction: row;
  gap: 5rem;
  margin: 3rem 0 7rem 0;
}
.footer__container .footer-nav a {
  color: hsl(168, 34%, 41%);
  font-size: 2.2rem;
}
.footer__container .footer-nav a:hover {
  color: hsl(0, 0%, 100%);
}
.footer__container .footer-social {
  display: flex;
  flex-direction: row;
  justify-content: center;
  gap: 3rem;
}

.social svg {
  transition: transform 0.5s ease-in-out;
}
.social svg:hover {
  transform: scale(1.5);
}
.social svg path {
  transition: fill 0.5s ease-in-out;
}
.social svg path:hover {
  fill: hsl(0, 0%, 100%);
}

.scrollup {
  position: fixed;
  right: 2rem;
  bottom: -50%;
  background-color: #3ebfff;
  padding: 0.6rem;
  border-radius: 0.5rem;
  box-shadow: 8px 9px 14px 0 #333;
  z-index: 10;
  transition: bottom 0.4s, transform 0.4s;
}
.scrollup:hover {
  transform: translateY(-0.8rem);
}

.show-scroll {
  bottom: 5rem;
}