@use 'base/variables' as v;


@mixin telefono {
    @media (min-width: v.$tablet) {
        @content;
    }
}

@mixin tablet {
    @media (min-width: v.$tablet) {
        @content;
    }
}
@mixin maxTablet {
    @media (max-width: v.$tablet) {
        @content;
    }
}
@mixin Desktop{
    @media (min-width: v.$desktop) {
        @content;
    }
}
@mixin maxDesktop{
    @media (max-width: v.$desktop) {
        @content;
    }
}
